	module.exports = {"pathContext":{"docHtml":"<h1 id=\"release-instructions\"><a href=\"#release-instructions\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Release Instructions</h1>\n<p>Allow 1h to do a release.</p>\n<h2 id=\"preparation\"><a href=\"#preparation\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Preparation</h2>\n<p>Cherry-pick your changes from master onto the release branch.</p>\n<p>The release branch should be green <a href=\"https://app.circleci.com/github/argoproj/argo/pipelines\">in CircleCI</a> before you start.</p>\n<h2 id=\"release\"><a href=\"#release\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Release</h2>\n<p>To generate new manifests and perform basic checks:</p>\n<pre><code>make prepare-release VERSION=v2.7.2\n</code></pre>\n<p>Publish the images and local Git changes:</p>\n<pre><code>make publish-release\n</code></pre>\n<p>Create <a href=\"https://github.com/argoproj/argo/releases\">the release</a> in Github. You can get some text for this using <a href=\"https://github.com/alexec/github-toolkit\">Github Toolkit</a>:</p>\n<pre><code>ght relnote v2.7.1..v2.7.2\n</code></pre>\n<p>Release notes checklist:</p>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> All breaking changes are listed with migration steps</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> The release notes identify every publicly known vulnerability with a CVE assignment </li>\n</ul>\n<p>If this is GA:</p>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> Update the <code>stable</code> tag</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> Update the <a href=\"https://github.com/argoproj/homebrew-tap\">Homebrew tap</a>.</li>\n</ul>","docPath":"argo/releasing","proj":"argo"}}

